import React, { Component } from 'react';
import {
  Card,
} from 'antd';
import Page from 'components/LayoutComponents/Page';
import TableGradeStudentView from './TableGradeStudentView';
import CardTitle from '../../../components/common/CardTitle/index';

class ClassOwnerActivity extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    return (
      <Card className="class-owner-activity">
        <CardTitle title="ng k TKB" />
        <TableGradeStudentView />
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ClassOwnerActivity />
  </Page>
);



