import React, { Component } from 'react';
import 'moment/locale/en-gb';
import {
  Table,
  Row,
  Col,
  Select,
  message,
} from 'antd';
import { getStudentCanRegisterTimetable, registerTimetable } from 'services/educationPortal';
import TableData from '../../../react-uikit/component/table/TableData';
import moment from '../../../../node_modules/moment-timezone/index';
import Btn from '../../../components/common/Btn/Btn';
import './style.scss';

const { Option } = Select;
const lessons = Array.from({ length: 14 }, (_, i) => i + 1);
const dayOfWeekList = {
  1: 'Th 2',
  2: 'Th 3',
  3: 'Th 4',
  4: 'Th 5',
  5: 'Th 6',
  6: 'Th 7',
  0: 'CN',
};

class TableRoomsForAssignSubject extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: false,
      dataRaw: [],
      dataShow: [],
      selectedRowKeys: [],
      selectedRows: [],
      selectedRowsRaw: [],
      studentId: null,
      subjectList: [],
      reload: 0,
    };
  }

  componentDidMount = () => {
    const studentId = JSON.parse(window.localStorage.getItem('app.claims')).userId;
    this.getData(studentId);
    this.setState({
      studentId,
    })
  };

  onSelectAll = () => {
    const { selectedRowKeys, selectedRows } = this.state;
    this.setState({
      selectedRowKeys,
      selectedRows,
    });
  }

  onFilterSubjectChange = (value) => {
    const { selectedRowsRaw } = this.state;
    const selectedRowKeysTemp = [];
    const selectedRowsTemp = [];
    let dataCurrent = [];
    if (value.length === 0) {
      const { dataRaw } = this.state;
      this.setState({
        dataShow: [...dataRaw],
      });
      dataCurrent = [...dataRaw];
    } else {
      const { dataRaw } = this.state;
      const dataTemp = dataRaw.filter(item => {
        return value.indexOf(item.subjectId) !== -1;
      });
      this.setState({
        dataShow: [...dataTemp],
      });
      dataCurrent = [...dataTemp];
    }
    selectedRowsRaw.forEach(subject => {
      dataCurrent.forEach((subjectCurrent, index) => {
        if (subject.ccsId === subjectCurrent.ccsId) {
          selectedRowKeysTemp.push(index);
          selectedRowsTemp.push(subjectCurrent);
        }
      });
    });
    this.setState({
      selectedRowKeys: [...selectedRowKeysTemp],
      selectedRows: [...selectedRowsTemp],
    });
  }

  onSelectChange = (selectedRowKeysOnChange, selectedRowsOnChange) => {
    const { selectedRowKeys, selectedRows, selectedRowsRaw, reload } = this.state;
    let isOverlap = false;
    const currentOldRowsCssid = selectedRows.map(subject => subject ? subject.ccsId : "");
    const currentNewRowsCssid = selectedRowsOnChange.map(subject => subject ? subject.ccsId : "");
    if (selectedRowsOnChange.length > 1 && selectedRowKeysOnChange.length > selectedRowKeys.length) {
      const differenceCssId = currentNewRowsCssid.filter(x => currentOldRowsCssid.indexOf(x) === -1)[0];
      const lastSubject = selectedRowsOnChange.find(subject => subject.ccsId === differenceCssId);
      const oldSubjectList = [...selectedRowsRaw];
      oldSubjectList.forEach(subject => {
        const checkIsOverlapDate = this.isOverlapDate(
          subject.fromDate,
          subject.toDate,
          lastSubject.fromDate,
          lastSubject.toDate
        );
        const checkIsOverLapDayOfWeek = this.isOverLapDayOfWeek(subject.dayOfWeek, lastSubject.dayOfWeek);
        const checkIsOverlapPeriod = this.isOverlapPeriod(
          subject.fromPeriodId,
          subject.toPeriodId,
          lastSubject.fromPeriodId,
          lastSubject.toPeriodId
        );
        if (checkIsOverlapDate && checkIsOverLapDayOfWeek && checkIsOverlapPeriod) {
          isOverlap = true;
        }
      });
    }
    if (isOverlap === true) {
      message.warn('Mn hc c Ngy hoc Th hoc Tit trng!!!')
      selectedRowKeysOnChange.pop();
      this.setState({
        selectedRowKeys: selectedRowKeysOnChange,
      });
    } else if (selectedRowKeys.length > selectedRowKeysOnChange.length) {
      const differenceCssId = currentOldRowsCssid.filter(x => currentNewRowsCssid.indexOf(x) === -1)[0];
      const diffSubject = selectedRows.find(subject => subject.ccsId === differenceCssId);
      const selectedRowsRawTemp = selectedRowsRaw.filter(subject => subject.ccsId !== diffSubject.ccsId);
      this.setState({
        selectedRowsRaw: [...selectedRowsRawTemp],
        selectedRows: [...selectedRowsOnChange],
        selectedRowKeys: [...selectedRowKeysOnChange],
        reload: reload + 1,
      });
    } else {
      const differenceCssId = currentNewRowsCssid.filter(x => currentOldRowsCssid.indexOf(x) === -1)[0];
      const diffSubject = selectedRowsOnChange.find(subject => subject.ccsId === differenceCssId);
      selectedRowsRaw.push({ ...diffSubject });
      this.setState({
        selectedRowsRaw,
        selectedRows: [...selectedRowsOnChange],
        selectedRowKeys: [...selectedRowKeysOnChange],
        reload: reload + 1,
      });
    }

  };

  getData = async (studentId) => {
    const { selectedRowKeys, selectedRows, reload } = this.state;
    this.setState({ loading: true });
    const [response, error] = await getStudentCanRegisterTimetable(studentId);
    this.setState({ loading: false });
    if (error) return;
    const dataRaw = response.data.items;
    this.initSubjects(dataRaw)
    // setup isSelected for table
    dataRaw.forEach((subject, index) => {
      if (subject.isSelected === true) {
        selectedRowKeys.push(index);
        selectedRows.push(subject);
      }
    });
    this.setState({
      selectedRowKeys,
      selectedRows,
      selectedRowsRaw: [...selectedRows],
      dataRaw,
      dataShow: [...dataRaw],
      reload: reload + 1,
    });
  };

  getColumnsTKB = () => {
    const tableColumns = [
      {
        title: 'Tit',
        key: 'lesson',
        dataIndex: 'lesson',
        width: 80,
        align: 'center',
      },
    ];
    Object.keys(dayOfWeekList).forEach(key => {
      tableColumns.push({
        title: dayOfWeekList[key],
        key: dayOfWeekList[key],
        render: (text, record) => {
          if (!record.data[key]) {
            return '';
          }
          const subject = record.data[key].find(item => item.fromPeriodId === record.lesson);
          if (!subject) {
            return '';
          }
          const rowSpan = subject.toPeriodId + 1 - subject.fromPeriodId;
          const obj = {
            children: (
              <div className="subject-item">
                <p className="subject-name">{subject.subject}</p>
                <p>{subject.room}</p>
                <p>{subject.teacher}</p>
              </div>
            ),
            props: {
              rowSpan,
              className: "subject-item"
            },
          };
          return obj;
        },
      });
    })
    return tableColumns;
  }


  isOverlapDate = (fromdateStringA, toDateStringA, fromdateStringB, toDateStringB) => {
    let isOverlap = false;
    const fromdateA = moment(fromdateStringA);
    const todateA = moment(toDateStringA);
    const fromdateB = moment(fromdateStringB);
    const todateB = moment(toDateStringB);
    if (
      (fromdateA <= fromdateB && fromdateB <= todateA) ||
      (fromdateA <= todateB && todateB <= todateA) ||
      (fromdateB <= fromdateA && fromdateA <= todateB) ||
      (fromdateB <= todateA && todateA <= todateB)) {
      isOverlap = true;
    }
    return isOverlap
  }

  isOverLapDayOfWeek = (dayOfWeekListA, dayOfWeekListB) => {
    let isOverlap = false;
    dayOfWeekListA.forEach(dayOfWeekA => {
      if (dayOfWeekListB.indexOf(dayOfWeekA) !== -1) {
        isOverlap = true;
      }
    });
    return isOverlap;
  }

  isOverlapPeriod = (FromPeriodIdA, ToPeriodIdA, FromPeriodIdB, ToPeriodIdB) => {
    let isOverlap = false;
    if (
      (FromPeriodIdA <= FromPeriodIdB && FromPeriodIdB <= ToPeriodIdA) ||
      (FromPeriodIdA <= ToPeriodIdB && ToPeriodIdB <= ToPeriodIdA) ||
      (FromPeriodIdB <= FromPeriodIdA && FromPeriodIdA <= ToPeriodIdB) ||
      (FromPeriodIdB <= ToPeriodIdA && ToPeriodIdA <= ToPeriodIdB)) {
      isOverlap = true;
    }
    return isOverlap;
  }

  initSubjects = (data) => {
    const subjectListTemp = [];
    const distinctList = [];
    data.forEach(item => {
      const subject = {
        value: item.subjectId,
        text: item.subject,
      };
      if (distinctList.indexOf(item.subjectId) === -1) {
        distinctList.push(item.subjectId);
        subjectListTemp.push(subject);
      }
    });
    this.setState({
      subjectList: [...subjectListTemp],
    });
  }

  handleGetDataResponse = res => {
    const [data] = res;
    const { items } = data.data;
    const dataTable = this.formatDataForTKB(items);
    return { content: lessons.map(l => ({ lesson: l, data: dataTable })), total: lessons.length };
  };

  formatDataForTKB = (items) => {
    const formateddata = items.reduce((obj, item) => {
      item.dayOfWeek.map(dayOfWeek => {
        if (!obj[dayOfWeek]) {
          obj[dayOfWeek] = [];
        }
        return obj[dayOfWeek].push(item);
      });
      return obj;
    }, {});
    return formateddata;
  }

  handleSaveTimeTable = async () => {
    const { studentId, selectedRowsRaw } = this.state;
    this.setState({ loading: true });
    const dataForm = {
      studentId,
      timetables: [...selectedRowsRaw]
    }
    const [response, error] = await registerTimetable(dataForm);
    this.setState({ loading: false });
    if (error) return;
    message.success('Lu thnh cng.');
    console.log(response);
  }

  columns = [
    {
      title: 'M hc phn',
      dataIndex: 'subjectId',
      key: 'subjectId',
    },
    {
      title: 'Tn hc phn',
      dataIndex: 'subject',
      key: 'subject',
    },
    {
      title: 'S TC',
      dataIndex: 'noOfCredit',
      key: 'noOfCredit',
    },
    {
      title: 'Tn lp TC',
      dataIndex: 'syllableCode',
      key: 'syllableCode',
    },
    {
      title: 'Ca hc',
      dataIndex: 'studySession',
      key: 'studySession',
    },
    {
      title: 'Lch hc',
      dataIndex: 'schedule',
      key: 'schedule',
      render: text => {
        return <div dangerouslySetInnerHTML={{ __html: text }} />;
      },
    },
    {
      title: 'Gio vin',
      dataIndex: 'teacher',
      key: 'teacher',
    },
    {
      title: 'Phng hc',
      dataIndex: 'room',
      key: 'room',
    },
    {
      title: 'Cn trng',
      dataIndex: 'remainingSlot',
      key: 'remainingSlot',
    },
  ];

  render() {
    const {
      loading,
      dataShow,
      selectedRowKeys,
      selectedRowsRaw,
      studentId,
      subjectList,
      reload
    } = this.state;
    const rowSelection = {
      selectedRowKeys,
      onChange: this.onSelectChange,
      onSelectAll: this.onSelectAll,
    };
    return (
      <div>
        <Row>
          <Col span={20}>
            <Select mode="tags" style={{ width: '100%' }} placeholder="B lc Mn hc" onChange={this.onFilterSubjectChange}>
              {subjectList.map(subject => (
                <Option key={subject.value} value={subject.value}>
                  {subject.text}
                </Option>
              ))}
            </Select>
          </Col>
          <Col span={4} style={{ textAlign: 'right' }}>
            <Btn primary disabled={selectedRowsRaw.length === 0} onClick={this.handleSaveTimeTable}>
              Lu
            </Btn>
          </Col>
        </Row>
        <Row>
          <Col span={24} >
            <Table
              loading={loading}
              rowSelection={rowSelection}
              columns={this.columns}
              dataSource={dataShow || []}
              rowKey="CCSId"
              bordered
              pagination={false}
              scroll={{ y: 500 }}
            />
          </Col>
        </Row>
        <Row>
          <Col span={24} style={{ textAlign: 'right' }}>
            <Btn primary disabled={selectedRowsRaw.length === 0} onClick={this.handleSaveTimeTable}>
              Lu
            </Btn>
          </Col>
        </Row>
        <Row className="page-Timetable">
          <Col span={24}>
            <TableData
              reload={reload}
              allowGetData={!!studentId}
              handleGetDataResponse={this.handleGetDataResponse}
              paramSearch={selectedRowsRaw}
              columns={this.getColumnsTKB()}
              service={async () => {
                return [{ data: { items: [...selectedRowsRaw] } }];
              }}
              pagination={false}
            />
          </Col>
        </Row>
      </div>
    );
  }
}

export default TableRoomsForAssignSubject;



